}

.main {
  background-color: #fafafa;
  flex: 1 0 auto;
}

.content {
  padding: 50px 0;
}



.navbar {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 75px;
  background: #7a52b3;
  z-index: 50;
}

.navbar__wrap {
  display: flex;
  justify-content: space-between;
  height: 100%;
  
}

.hamb {
  display: none;
}

.popup {
  display: none;
}

.logo {
   
    display: flex;
    align-items: center;
    img{
        height: 60px;
        width: 60px;
    }
  }

 .menu{
  width: 660px;
  margin: 0 auto 30px;
}
#ul{
  list-style: none;
  margin: 0;
  padding: 0;
  }
.topmenu > li{
  display: inline-block;
  margin-right: 20px;
  position: relative;
  
  }
  .topmenu > li:last-child{
    margin-right: 0;
  }
  .topmenu>li>a {
    display: block;
    padding: 10px 15px;
    text-decoration: none;
    outline: none;
    font-size: 25px;
    transform: .3s linear;
    color: #fff;
 }
 .fa fa-angle-down:hover{
  background: #7a52b3;
 
 }
 
 .fa fa-angle-down{
  background: #7a52b3;
  right:20px;
  color: #fff;
 }
 .fa {

  padding-left: 10px;
 
  
 }
 .submenu{
  padding: 10px 10px 10px 10px;
  border-radius: 10px;
background-color: #fff;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 270%;
  z-index: 70;
  transition: 0.8s ease-in-out;
  display: none;
  font-size: 20px;
  
 }
 
 .menu ul li:hover  .submenu{
  display: block;
  transition: 0.8s;
 }




.navbar__wrap .menu {
  display: flex;
}

.navbar__wrap .menu > li {
  display: flex;
  align-items: stretch;
}

.navbar__wrap .menu > li > a {
  display: flex;
  align-items: center;
  padding: 0 20px;
  color: rgba(255, 255, 255, 0.7);
}

.navbar__wrap .menu > li > a:hover {
  color: rgba(255, 255, 255, 1);
}

@media (max-width: 767px) {
  .topmenu>li{
  margin-bottom: 120px;

 }
