  font-size: 20px;
  display: flex;
  margin: 0 auto; 
  font-family: Constantia, "Lucida Bright", "DejaVu Serif", Georgia, "serif";
position: relative;
padding-bottom: 20px
}
.gitduets {
  width: 100%;
  min-width: 0px;
  display: flex;
  justify-content:space-around; 
}
.gitduets1,.gitduets2 {
  box-shadow: 0px 5px 5px black;
  height: 100%;
  background-color: #e2d7d7;
  border-radius: 10px;
  padding-left: 5px;
  padding-top: 10px;
  padding-right: 5px;
  padding-bottom: 10px;
  width: 320px;
  line-height: 30px;
 
 p:nth-child(even){
  background-color: #bac6d7;
     }
  p:before {
      content: "";
      width: 30px;
      height: 30px;
   
      background-image: url(/img/pdf.png);
      display: inline-block;
      margin: 0px 2px 0px;
     
  }
    p:visited {
    color: rgb(64, 50, 50);
}

  a:hover {
    border-radius: 15px;
    background-color: #e2d7d7;
      cursor: pointer;
      transition: 0.3s;
  }

 
  p:active {
    color: rgb(4, 4, 28);
}
}
.textgitduets::before{
  position: absolute;
  content: "";
  background-image: url(img/guitarsmall.gif);
width: 120px;
height: 80px;
right: 250px;
bottom: 5px;
}



