/*slider*/
.parallax-slider{
  height: 410px;
  width: 100%;
  min-width: 376px;
  overflow: hidden;
  border-radius: 10px;
  background: url('img/FonSky.jpg') right;
  background-attachment:local;
  display: flex;
  box-shadow: 0px 5px 5px black;
}
.parallax-slider-text{
 padding-top:74px;
 padding-left: 40%;
 padding-right: 45%;
 padding-bottom: 0%;

  
  font-size: 20.8px;
  font-weight:bold;
  color:#7a52b3;
  text-transform: uppercase;
}

  

@media (max-width: 767px){
  .parallax-slider-text{
    padding-top:10%;
 padding-left: 35%;
 padding-right: 45%;
 padding-bottom: 0%;
  }
}
@media (max-width: 509px){
  .parallax-slider-text{
    padding-top:10%;
 padding-left: 0%;

 padding-bottom: 0%;
  }
 
}
@media (max-width: 801px){
  
}
@media (max-width: 1186px){
  .parallax-slider-text{
    padding-top:13%;
 padding-left: 35%;
 padding-right: 45%;
 padding-bottom: 0%;
  }
}
.image-slider1 {
  height: 410px;
  width: 55%;
  min-width: 500px;
  
}

.swiper-conteiner{
  position: absolute;
}


.image-slider1-image {
  text-align: center;
  height: 426px;
 min-width: 500px;
  overflow: hidden;
  border-radius: 10px;
}

.image-slider1-image img{

  height: 410px;
  object-fit: cover;
  border-radius: 10px;
} 
.parallax-slider .swiper-button-prev::after,
.parallax-slider  .swiper-button-next::after {
  color:  #b280f7;
  font-weight: bold;
  font-size: 40px;
 
}

.image-slider1 .swiper-button-prev::after,
.image-slider1  .swiper-button-next::after {
  color:  #7a52b3;
  font-weight: bold;
  display: none;
  
  font-size: 45px;

}


.swiper-pagination {

margin-top: 0px;
}
.swiper-pagination-bullet{

width: 20px;
height: 20px;
line-height: 20px;
font-size: 20px;
background-color: #7a52b3 ;
}
.swiper-scrollbar-drag{
  cursor: pointer;
  background-color: #b086eb;
}
















